* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
  
body {
    font-family: "Segoe UI", Arial, sans-serif;
    line-height: 1.4;
    color: #444;
    background: #fff;
    height: 100vh;
}

#root {
  height: 100%;
}

.jimmy {
  background-image: url(https://www.aesdes.org/wp-content/uploads/2019/01/Screen-Shot-2019-01-22-at-6.17.13-PM-1024x773.jpg);
  background-blend-mode: exclusion;
  background-size: cover;
  background-position: center;
  height: 100%;
}

.headerbox {
  background-color: #d3d3d3;
  border-radius: 25px;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
}
  
.inner {
    width: 100%;
    padding: 8rem 10px 4rem;
}
  
.form-container {
    width: 100%;
    margin-bottom: 20px;
    display: flex;
    background-color: whitesmoke;
    border-radius: calc(0.5 * 100px);
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.38);
    justify-content: space-evenly;
}
  
.input-text {
    font-size: 1rem;
    font-weight: 400;
    width: 85%;
    padding-right: 5px;
    padding-left: 10px;
    border-radius: calc(0.5 * 100px);
}
  
.input-text::placeholder {
    color: #000;
}
  
.input-submit {
    background: transparent;
    color: #5b5b5b;
    text-transform: capitalize;
    cursor: pointer;
    font-weight: 600;
    margin-right: 10px;
}
  
.input-text,
.input-submit {
    height: 45px;
    outline: none;
    border: none;
}

.active-link {
    color: orangered;
    text-decoration: underline;
  }

  .navBar {
    position: relative;
  }
  
  .navBar button {
    position: fixed;
    left: 40px;
    top: 40px;
    z-index: 10;
    cursor: pointer;
    background: solid;
    background-color: #d3d3d3;
    border-radius: 10px;
  }
  
  .menuNav {
    overflow-y: scroll;
    list-style: none;
    position: fixed;
    top: 0;
    background: darkcyan;
    left: 0;
    bottom: 0;
    height: 100vh;
    width: 0;
    overflow: hidden;
    max-width: 290px;
    z-index: 9;
  }
  
  .menuNav.showMenu {
    width: 100%;
  }
  
  a {
    display: block;
    padding: 10px 40px;
    text-decoration: none;
    color: #3fffd2;
    text-transform: uppercase;
    font-weight: bold;
  }
  
  .menuNav li:first-child {
    margin-top: 7rem;
  }

  .about__content {
    padding: 8rem 40px;
    display: flex;
  }
  
  .about__list {
    list-style: none;
    flex: 2;
    margin-right: 15px;
  }
  
  .about__list a {
    color: #9f9f9f;
    text-transform: capitalize;
    padding: 20px 0;
    font-weight: 600;
  }
  
  .about__list a:hover {
    color: #000;
  }
  
  .main__content {
    flex: 7;
    background-color: #d3d3d3;
    color: black;
    padding: 15px;
    border-radius: 25px;
  }
  
  .main__content h1 {
    margin-bottom: 15px;
    line-height: 30px;
    font-size: 30px;
  }
  
  .main__content p {
    line-height: 25px;
  }

